'use client';

import { Button } from '@/components/ui/button';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { zodResolver } from '@hookform/resolvers/zod';
import {
  Document,
  Page,
  pdf,
  PDFViewer,
  StyleSheet,
  Text,
  View,
} from '@react-pdf/renderer';
import { saveAs } from 'file-saver';
import { DownloadIcon, EyeIcon } from 'lucide-react';
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { z } from 'zod';
import { User } from '../types/user';
import CustomSelect from './CustomSelect';

const formSchema = z.object({
  name: z
    .string()
    .min(3, 'Nama minimal 3 karakter')
    .max(50, 'Nama maksimal 50 karakter'),
  email: z.string().email('Email tidak valid').min(5, 'Email terlalu pendek'),
  phone: z.string().optional(),
  companyName: z
    .string()
    .max(100, 'Nama perusahaan maksimal 100 karakter')
    .optional(),
  street: z.string().max(100).optional(),
  city: z.string().max(50).optional(),
  zipcode: z.string().max(20).optional(),
});

type FormValues = z.infer<typeof formSchema>;

const styles = StyleSheet.create({
  page: {
    padding: 40,
    fontFamily: 'Helvetica',
    backgroundColor: '#ffffff',
  },
  header: {
    fontSize: 24,
    fontWeight: 'bold',
    textAlign: 'center',
    marginBottom: 20,
    color: '#000000',
    borderBottom: '2px solid #000000',
    paddingBottom: 5,
  },
  section: {
    marginBottom: 15,
    padding: 10,
    border: '1px solid #e2e8f0',
    borderRadius: 5,
    backgroundColor: '#f8fafc',
  },
  sectionTitle: {
    fontSize: 16,
    fontWeight: 'bold',
    color: '#000000',
    marginBottom: 8,
    borderBottom: '1px dashed #9ca3af',
  },
  text: {
    fontSize: 12,
    color: '#000000',
    lineHeight: 1.5,
    marginBottom: 5,
  },
  row: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    marginBottom: 5,
  },
  label: {
    fontSize: 12,
    fontWeight: 'bold',
    color: '#000000',
    width: '30%',
  },
  value: {
    fontSize: 12,
    color: '#000000',
    width: '70%',
  },
  footer: {
    position: 'absolute',
    bottom: 20,
    left: 40,
    right: 40,
    textAlign: 'center',
    fontSize: 10,
    color: '#E1E1E1',
    fontStyle: 'italic',
    borderTop: '1px solid #e2e8f0',
    paddingTop: 5,
  },
  watermark: {
    fontSize: 20,
    color: 'rgba(59, 130, 246, 0.1)',
    fontWeight: 'bold',
    textAlign: 'center',
    transform: 'rotate(-45deg)',
  },
  watermarkContainer: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flexDirection: 'column',
    justifyContent: 'space-between',
    alignItems: 'center',
    zIndex: -1,
  },
  watermarkRow: {
    flexDirection: 'row',
    justifyContent: 'space-around',
    width: '100%',
  },
});

const UserProfilePDF = ({
  user,
  template = 'simple',
}: {
  user: FormValues;
  template?: 'simple' | 'detailed';
}) => (
  <Document>
    <Page size='A4' style={styles.page}>
      {/* Watermark Full-Page */}
      {user.companyName && (
        <View style={styles.watermarkContainer} fixed>
          {/* Pola grid: 5 baris x 5 kolom */}
          <View style={styles.watermarkRow}>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
          </View>
          <View style={styles.watermarkRow}>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
          </View>
          <View style={styles.watermarkRow}>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
          </View>
          <View style={styles.watermarkRow}>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
          </View>
          <View style={styles.watermarkRow}>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
            <Text style={styles.watermark}>{user.companyName}</Text>
          </View>
        </View>
      )}

      {/* Header */}
      <Text style={styles.header}>
        {template === 'simple' ? 'Profil Pengguna' : 'Detail Profil Pengguna'}
      </Text>

      {/* Konten Utama */}
      {template === 'simple' ? (
        <View style={styles.section}>
          <View style={styles.row}>
            <Text style={styles.label}>Nama:</Text>
            <Text style={styles.value}>{user.name}</Text>
          </View>
          <View style={styles.row}>
            <Text style={styles.label}>Email:</Text>
            <Text style={styles.value}>{user.email}</Text>
          </View>
          {user.phone && (
            <View style={styles.row}>
              <Text style={styles.label}>Telepon:</Text>
              <Text style={styles.value}>{user.phone}</Text>
            </View>
          )}
        </View>
      ) : (
        <View>
          {/* Bagian Identitas */}
          <View style={styles.section}>
            <Text style={styles.sectionTitle}>Informasi Pribadi</Text>
            <View style={styles.row}>
              <Text style={styles.label}>Nama Lengkap:</Text>
              <Text style={styles.value}>{user.name}</Text>
            </View>
            <View style={styles.row}>
              <Text style={styles.label}>Email:</Text>
              <Text style={styles.value}>{user.email}</Text>
            </View>
            {user.phone && (
              <View style={styles.row}>
                <Text style={styles.label}>Telepon:</Text>
                <Text style={styles.value}>{user.phone}</Text>
              </View>
            )}
          </View>

          {/* Bagian Alamat */}
          {(user.street || user.city || user.zipcode) && (
            <View style={styles.section}>
              <Text style={styles.sectionTitle}>Alamat</Text>
              {user.street && (
                <View style={styles.row}>
                  <Text style={styles.label}>Jalan:</Text>
                  <Text style={styles.value}>{user.street}</Text>
                </View>
              )}
              {user.city && (
                <View style={styles.row}>
                  <Text style={styles.label}>Kota:</Text>
                  <Text style={styles.value}>{user.city}</Text>
                </View>
              )}
              {user.zipcode && (
                <View style={styles.row}>
                  <Text style={styles.label}>Kode Pos:</Text>
                  <Text style={styles.value}>{user.zipcode}</Text>
                </View>
              )}
            </View>
          )}

          {/* Bagian Perusahaan */}
          {user.companyName && (
            <View style={styles.section}>
              <Text style={styles.sectionTitle}>Perusahaan</Text>
              <View style={styles.row}>
                <Text style={styles.label}>Nama Perusahaan:</Text>
                <Text style={styles.value}>{user.companyName}</Text>
              </View>
            </View>
          )}
        </View>
      )}

      {/* Footer */}
      <Text style={styles.footer}>
        {user.companyName
          ? `Dibuat oleh: ${
              user.companyName
            } | ${new Date().toLocaleDateString()}`
          : `Dibuat pada: ${new Date().toLocaleDateString()}`}
      </Text>
    </Page>
  </Document>
);

export default function EditUserForm({ user }: { user: User }) {
  const [isPreviewOpen, setIsPreviewOpen] = useState(false);
  const [template, setTemplate] = useState<'simple' | 'detailed'>('simple');
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: user.name,
      email: user.email,
      phone: user.phone,
      companyName: user.company?.name || '',
      street: user.address?.street || '',
      city: user.address?.city || '',
      zipcode: user.address?.zipcode || '',
    },
    mode: 'onChange',
  });

  const formData = form.watch();

  const onSubmit = async (data: FormValues) => {
    const pdfBlob = await pdf(
      <UserProfilePDF user={data} template={template} />
    ).toBlob();
    saveAs(pdfBlob, `${data.name}_profile.pdf`);
  };

  return (
    <>
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className='space-y-6'>
          {/* Field Nama */}
          <FormField
            control={form.control}
            name='name'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <UserIcon className='h-5 w-5 text-blue-600' />
                  <span>Nama</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='John Doe'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />

          {/* Field Email */}
          <FormField
            control={form.control}
            name='email'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <EmailIcon className='h-5 w-5 text-blue-600' />
                  <span>Email</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='john@example.com'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />

          {/* Field Telepon */}
          <FormField
            control={form.control}
            name='phone'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <PhoneNumberIcon className='h-5 w-5 text-blue-600' />
                  <span>Telepon</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='+6281234567890'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />

          {/* Field Perusahaan */}
          <FormField
            control={form.control}
            name='companyName'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <CompanyIcon className='h-5 w-5 text-blue-600' />
                  <span>Nama Perusahaan</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='Perusahaan ABC'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />

          {/* Field Alamat */}
          <FormField
            control={form.control}
            name='street'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <AddressIcon className='h-5 w-5 text-blue-600' />
                  <span>Jalan</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='Jalan Contoh 123'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name='city'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <CityIcon className='h-5 w-5 text-blue-600' />
                  <span>Kota</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='Jakarta'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name='zipcode'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <PostalCodeIcon className='h-5 w-5 text-blue-600' />
                  <span>Kode Pos</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='12345'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />

          {/* Informasi Tambahan */}
          <div className='bg-gray-100 p-4 rounded-2xl'>
            <h3 className='text-lg font-semibold text-gray-700 mb-2'>
              Informasi Tambahan
            </h3>
            <p className='text-gray-600 mb-2'>Username: {user.username}</p>
            <p className='text-gray-600 mb-2'>Website: {user.website}</p>
            {user.address?.geo && (
              <p className='text-gray-600'>
                Koordinat: Lat {user.address.geo.lat}, Lng{' '}
                {user.address.geo.lng}
              </p>
            )}
          </div>

          {/* Bagian Template dan Tombol */}
          <div className='bg-blue-50 p-6 rounded-2xl border border-blue-100 space-y-4'>
            <div>
              <label className='block text-sm font-semibold text-gray-700 mb-3 flex items-center space-x-2'>
                <span>Pilih Template PDF</span>
              </label>
              <CustomSelect
                template={template}
                setTemplate={setTemplate}
                onToggle={setIsDropdownOpen}
              />
            </div>
            <div
              className={`flex space-x-3 transition-transform duration-300 ease-in-out ${
                isDropdownOpen ? 'translate-y-4' : 'translate-y-0'
              }`}
            >
              <Button
                type='button'
                onClick={() => setIsPreviewOpen(true)}
                className='bg-blue-600 hover:bg-blue-700 text-white rounded-xl px-6 py-3 transition-all transform hover:scale-105 flex items-center space-x-2'
              >
                <EyeIcon className='h-5 w-5' />
                <span>Pratinjau PDF</span>
              </Button>
              <Button
                type='submit'
                disabled={!form.formState.isValid}
                className='bg-green-600 hover:bg-green-700 text-white rounded-xl px-6 py-3 transition-all transform hover:scale-105 flex items-center space-x-2 disabled:opacity-70'
              >
                <DownloadIcon className='h-5 w-5' />
                <span>Unduh PDF</span>
              </Button>
            </div>
          </div>
        </form>
      </Form>

      {/* Preview PDF */}
      {isPreviewOpen && (
        <div className='fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center p-6 z-50 animate-fade-in'>
          <div className='bg-white rounded-2xl w-full max-w-5xl h-[90vh] flex flex-col shadow-2xl overflow-hidden'>
            <div className='p-4 border-b border-gray-200 flex justify-between items-center bg-blue-50'>
              <h3 className='text-lg font-semibold text-blue-600'>
                Pratinjau PDF - {formData.name}
              </h3>
              <Button
                variant='outline'
                onClick={() => setIsPreviewOpen(false)}
                className='px-x py-2 flex items-center space-x-2 cursor-pointer'
              >
                {/* <XIcon className='h-5 w-5' /> */}x
              </Button>
            </div>
            <div className='flex-1 p-6 bg-gray-50'>
              <PDFViewer
                width='100%'
                height='100%'
                className='w-full h-full rounded-lg shadow-inner border border-gray-200'
              >
                <UserProfilePDF user={formData} template={template} />
              </PDFViewer>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
